<template>
  <div class="tmpl">
    <nav-bar title="图文介绍"></nav-bar>
    <div class="news-title">
      <p v-text="goodsInfo.title"></p>
      <div class="news-info">
        <span>点击：{{goodsInfo.click_num}}</span>
        <span>分类：商品</span>
        <span>发布时间：{{goodsInfo.add_time|filterTime}}</span>
      </div>
    </div>
    <div class="news-content" v-html="goodsInfo.desc"></div>
  </div>
</template>

<script>
    export default {
      name: "GoodsInfo",
      data(){
        return{
          goodsInfo:{}//新闻详情对象
        }
      },
      created(){
        // 获取路由参数
        let id = this.$route.query.id;
        // 发起请求
        this.$axios.get(this.$host+'/api/goodsdetail/'+id)
          .then(res=>{
            console.log(res.data.data);
            this.goodsInfo = res.data.data;
          })
          .catch(err=>{
            console.log(err);
          })
      }
    }
</script>

<style scoped>
  .news-title{
    margin-top: 5px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
  }
  .news-title p{
    color: #0a87f8;
    font-size: 20px;
    font-weight: bold;
  }
  .news-title span{
    margin-right: 30px;
  }
  .news-info{
    font-size: 14px;
  }
  /*主体文章的左右标题*/
  .news-content{
    padding: 5px;
  }
</style>



